import { Button, Center, Heading, HStack, IconButton, VStack } from '@chakra-ui/react'
import React, { Dispatch, SetStateAction, useState } from 'react'
import { IconType } from 'react-icons'
import {RiEmotionFill, RiEmotionNormalFill, RiEmotionHappyFill, RiEmotionUnhappyFill} from 'react-icons/ri'
import UseThemeColors from '../Hooks/useThemeColors'

interface Emotion {
  icon: IconType,
  emotionColor: string,
  emotionName: string

}
//slider colors correspond to < 25 <50 <75 76+
const emotions: Emotion[] = 
  [
    {icon: RiEmotionUnhappyFill, emotionColor: "purple.900", emotionName: "Awful"}, 
    {icon: RiEmotionNormalFill, emotionColor: "blue.700", emotionName:"Meh"},
    {icon: RiEmotionHappyFill, emotionColor: "green.300", emotionName: "Good" },
    {icon: RiEmotionFill, emotionColor: "yellow.300", emotionName: "Awesome"}
  ]


interface EmotionButtonProps {
  emotion: Emotion
  setSelected: Dispatch<SetStateAction<string>>,
  selected: string 
}
const EmotionButton: React.FC<EmotionButtonProps> = ({emotion, setSelected, selected}) => {
  const {backgroundColor} = UseThemeColors()
  const {emotionName, emotionColor, icon} = emotion
  const getIconColor = (emotionName: string) => {
    if(selected && emotionName !== selected) {
      return "black"
    } else {
      return emotionColor
    }
  }
  const handleOnClick = (emotionName: string) => {
    if(emotionName === selected) {
      setSelected("")
    } else {
      setSelected(emotionName)
    }
  }
  return (
    <>
      <IconButton 
        size={"lg"}
        _hover={{background:backgroundColor}}
        _active={{background:backgroundColor}}
        onClick={() => handleOnClick(emotionName)}
        aria-label={`${emotionName} button`}  
        as={icon} 
        bg={backgroundColor}
        color={getIconColor(emotionName)}
        
      />
    </>
  )
}

interface EmotionPickerProps {
  onSubmit: (emotion: Emotion) => void
}
const getEmotionColorByName = (emotionName: string) => {
  return emotions.filter(emotion => emotion.emotionName === emotionName)[0]?.emotionColor
}

const EmotionPicker: React.FC<EmotionPickerProps> = ({onSubmit}) => {
  const [selected, setSelected] = useState<string>("")
  const emotionColor = getEmotionColorByName(selected)
  const {foregroundColor} = UseThemeColors()


  return (
    <>
      <Center rounded={"75px"} border={selected ? "15px solid" : "none"} borderColor={emotionColor}  p={4} height={"full"} minHeight={"90%"} flexDir={"column"}> 
        <Heading position={"fixed"} top={"25vh"}> How do you feel? </Heading>
        <HStack height={"50%"} w={"100%"} spacing={12}> 
          {emotions.map(emotion => 
            <EmotionButton emotion={emotion} selected={selected} setSelected={setSelected} />

          )}
        
        </HStack>
        <VStack p={2} w={"full"} height={"15vh"} spacing={12}>
          {selected
            ? 
            <>
              <Heading color={emotionColor}>  {selected} </Heading>
              <Button w={"60%"} minH={"60px"} bg={"white"} > Submit </Button>
            </>
            : null
          }
        </VStack>
       
      </Center>
    </>
  )
}

export default EmotionPicker